html,
body {
	height: 100%; }

body {
	min-width: 320px;
	background: $black;
	@include f('light', 20, 22); }

.out {
	overflow: hidden;
	min-height: 100%; }

.inner {
	position: relative;
	z-index: 10;
	margin: 0 auto;
	max-width: 980px;
	@include clr;
	@include lg {
		max-width: 700px; }
	@include md {
		max-width: 500px; } }

.decor {
	position: relative;
	max-width: 190px;
	border-bottom: 5px solid $yellow;
	@include lg {
		border-bottom-width: 10px;
		max-width: 211px; }
	&:before,
	&:after {
		content: "";
		position: absolute;
		top: 0;
		left: 50%;
		transform: translate3d(-50%,0,0); }
	&:after {
		@include arr(32,11,$black,b);
		@include lg {
			@include arr(34,11,$black,b); } }
	&:before {
		@include arr(48,17,$yellow,b);
		@include lg {
			@include arr(70,22,$yellow,b); } } }

//typography
h1, h2, h3, h4, h5, h6, strong, b, cite, th, i, em {
	font-weight: normal;
	font-style: normal; }

a {
	color: inherit;
	text-decoration: none; }

.btn {
	display: inline-block;
	vertical-align: top;
	padding: 15px 20px 11px;
	color: $white;
	background: $black;
	@include f('mreg', 23, 23);
	text-align: center;
	transition: background 0.25s linear;
	&:hover {
		background: lighten($black, 20%); }
	@include lg {
		padding: 19px 20px 13px;
		width: 100%;
		@include f(null, 48, 60); }
	@include sm {
		padding: 19px 30px 13px;
		width: auto;
		@include f(null, 24, 30); }
	span {
		@include lg {
			display: block; } }
	em {
		display: none;
		@include f('mbold');
		@include lg {
			display: inline-block; } }
	&_yellow {
		span {
			@include lg {
				color: $yellow; } } }
	&_white {
		color: $black;
		background: $white;
		@include lg {
			background: $yellow; }
		&:hover {
			color: $black;
			background: darken($white, 10%);
			@include lg {
				background: lighten($yellow, 10%); } }
		em {
			display: inline-block; }
		em, br {
			@include lg {
				display: none; } }
		strong {
			@include lg {
				display: block;
				@include f('mbold'); } } } }
