.lil-info {
	padding-top: 20px;
	&__inner {
		position: relative;
		margin: 0 auto;
		max-width: 980px;
		@include lg {
			max-width: 700px; } }
	&__figure {
		position: relative;
		float: left;
		width: 51%;
		@include lg {
			width: 100%; } }
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		max-width: 100%;
		@include lg {
			max-width: 90%; } }
	&__img-desktop {
		@include lg {
			left: auto;
			right: 0; }
		@include md {
			display: none; } }
	&__img-mobile {
		display: none;
		@include md {
			display: block; }
		@include sm {
			max-width: 300px; } }
	&__content {
		position: relative;
		z-index: 2;
		float: right;
		padding: 65px 0 91px 70px;
		width: 49%;
		color: $white;
		@include lg {
			padding: 100px 0 65px;
			width: 100%; }
		@include md {
			padding: 100px 0px 65px; }
		@include sm {
			padding: 30px 20px 65px; } }
	&__text {
		max-width: 320px;
		@include f(null, 25, 27);
		@include lg {
			@include f(null, 36, 39); }
		@include sm {
			@include f(null, 25, 28); }
		@include xs {
			@include f(null, 18, 22); }
		em {
			color: $yellow; } }
	&__top {
		max-width: 270px;
		@include lg {
			max-width: 270px; }
		@include md {
			float: right; }
		@include sm {
			max-width: 200px; }
		@include xs {
			max-width: 140px; } }
	&__main {
		@include lg {
			max-width: 100%; }
		@include md {
			clear: both; } }
	&__box {
		padding: 38px 20px 38px 30px;
		margin: 25px 0 23px;
		color: $black;
		background: $yellow;
		@include f('reg', 42, 44);
		@include lg {
			padding: 47px 46px;
			margin: 205px 0 20px;
			@include f(null, 51, 53); }
		@include md {
			clear: both;
			float: left;
			width: 100%; }
		@include sm {
			padding: 25px 25px;
			margin: 100px 0 20px;
			@include f(null, 29, 32); }
		@include xs {
			@include f(null, 26, 28); } }
	&__meta {
		@include lg {
			float: right; }
		&-text {
			padding: 50px 0 15px;
			@include f('med', 37, 38);
			@include lg {
				padding: 40px 0 15px;
				@include f(null, 42, 44); }
			@include sm {
				@include f(null, 34, 36); } } }
	&__decor {
		i {
			position: absolute;
			top: 100%;
			left: 50%;
			transform: translate3d(-50%,0,0);
			border-left: 5px solid $yellow;
			height: 96px;
			@include lg {
				display: none; } } }
	&__exclamation {
		position: absolute;
		top: 216px;
		right: -41px;
		@include s(exclamation);
		@include lg {
			display: none; } } }
