.lesson {
	&__inner {
		position: relative;
		padding-bottom: 40px;
		margin: 0 auto;
		max-width: 980px;
		@include lg {
			max-width: 100%; } }
	&__bg {
		position: relative;
		z-index: 1;
		background: $yellow; }
	&__content {
		padding: 60px 40px 80px 97px;
		@include lg {
			padding: 40px 0 75px; }
		@include sm {
			padding: 45px 20px 50px; } }
	&__frame {
		padding: 76px 80px 40px 0;
		@include lg {
			padding: 0 0 50px; }
		@include sm {
			padding: 0 20px 50px; } }
	&__content,
	&__frame {
		float: left;
		width: 50%;
		@include lg {
			width: 100%; } }
	&__video {
		margin-bottom: 131px;
		@include lg {
			margin-bottom: 100px; }
		@include md {
			margin-bottom: 70px; }
		video {
			width: 100%; } }
	&__title {
		@include f('light', 54, 54);
		@include lg {
			font-size: 85px; }
		@include sm {
			font-size: 40px; } }
	&__number {
		@include f('med', 152, 124);
		@include lg {
			@include f(null, 239, 195); }
		@include sm {
			@include f(null, 110, 105); } }
	&__subtitle {
		margin-bottom: 30px;
		@include f('light', 72, 50);
		@include lg {
			@include f(null, 114, 80); }
		@include sm {
			@include f(null, 60, 45); } }
	&__text {
		margin-bottom: 17px;
		max-width: 310px;
		@include f('light', 20, 21);
		@include lg {
			max-width: 100%;
			@include f(null, 28, 29); }
		@include xs {
			@include f(null, 18, 22); } }
	&__meta {
		@include f('reg', 47, 44);
		@include xs {
			@include f(null, 33, 36); } }
	&__btn {
		text-align: right;
		@include sm {
			text-align: center; } }
	&__bottom {
		position: absolute;
		z-index: 2;
		bottom: 0;
		left: 0;
		right: 0;
		padding: 0 15px;
		height: 42px;
		font-size: 0;
		text-align: right;
		background: url(../img/lesson.png) no-repeat right bottom;
		background-size: cover; } }
