@function em($size, $context: 16) {
  @return ($size/$context) * 1em; }

@function img-url($image) {
  @return url('../img/#{$image}'); }

@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin smooth-scroll {
  -webkit-overflow-scrolling: touch; }

@mixin smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin gpu {
  transform: translate3d(0,0,0); }

@mixin lg {
  @include r(1200) {
    @content; } }

@mixin md {
  @include r(800) {
    @content; } }

@mixin sm {
  @include r(600) {
    @content; } }

@mixin xs {
  @include r(420) {
    @content; } }


@include font('PFDinPro Bold', pfdintextpro-bold);
@include font('PFDinPro Light', pfdintextpro-light);
@include font('PFDinPro Thin', pfsquaresanspro-thin);
@include font('PFDinPro Regular', pfdintextpro-regular);
@include font('PFDinPro Medium', pfdintextpro-medium);
@include font('MyriadPro Regular', MyriadPro-Regular);
@include font('MyriadPro Bold', MyriadPro-Bold);

@mixin f($name: null, $size: null, $line-height: null, $spacing: null) {
  $font-family: null;
  @if $name == 'thin' {
    $font-family: 'PFDinPro Thin', Arial, sans-serif; }
  @if $name == 'light' {
    $font-family: 'PFDinPro Light', Arial, sans-serif; }
  @if $name == 'reg' {
    $font-family: 'PFDinPro Regular', Arial, sans-serif; }
  @if $name == 'med' {
    $font-family: 'PFDinPro Medium', Arial, sans-serif; }
  @if $name == 'bold' {
    $font-family: 'PFDinPro Bold', Arial, sans-serif; }
  @if $name == 'mreg' {
    $font-family: 'MyriadPro Regular', Arial, sans-serif; }
  @if $name == 'mbold' {
    $font-family: 'MyriadPro Bold', Arial, sans-serif; }
  @if $name != null {
    font-family: $font-family; }
  @if $size != null {
    font-size: $size * 1px; }
  @if $line-height != null {
    line-height: (ceil(($line-height / $size)*100)/100); }
  @if $spacing != null {
    letter-spacing: ($spacing / $size) + em; } }

$black: #000;
$white: #fff;
$yellow: #ebbf00;
