.header {
	&__inner {
		position: relative;
		z-index: 5;
		margin: 0 auto;
		max-width: 980px; }
	&__figure {
		position: absolute;
		top: 81px;
		left: -150px;
		@include lg {
			position: relative;
			top: 0;
			left: 50%;
			display: block;
			margin: 0 0 -80px;
			transform: translate3d(-50%,0,0); }
		@include sm {
			display: none; }
		&-mobile {
			display: none;
			@include sm {
				display: block;
				margin: 0 auto;
				max-width: 100%; } } }
	&__content {
		float: right;
		padding: 108px 0 0;
		width: 48.5%;
		color: $white;
		@include lg {
			padding-top: 0;
			width: 100%; }
		@include sm {
			padding: 0 20px; } }
	&__title {
		margin-bottom: 20px;
		@include f('med', 54, 54);
		@include lg {
			position: absolute;
			bottom: 100%;
			right: 0;
			padding: 39px 0;
			border: 8px solid $white;
			border-radius: 50%;
			width: 195px;
			height: 195px;
			@include f(null, 32, 35);
			text-transform: uppercase;
			text-align: center; }
		@include sm {
			right: 20px;
			padding: 30px 0 20px;
			border: solid 5px #fff;
			width: 115px;
			height: 115px;
			@include f(null, 20, 20); }
		span {
			color: $yellow;
			@include lg {
				display: block;
				color: $white;
				@include f(null, 59, 62); }
			@include sm {
				@include f(null, 30, 30); } } }
	&__meta {
		margin-bottom: 45px;
		@include f('light', 21, 20);
		@include lg {
			display: none; } }
	&__subtitle {
		margin-bottom: 50px;
		color: $yellow;
		@include f('med', 70, 63);
		@include lg {
			margin-bottom: 30px;
			@include f(null, 77, 75); }
		@include sm {
			margin-top: 10px;
			@include f(null, 43, 40); }
		em {
			@include f('thin'); } }
	&__text {
		max-width: 410px;
		p {
			@include f('light', 41, 40);
			margin-bottom: 23px;
			@include lg {
				margin-bottom: 42px; }
			@include sm {
				margin-bottom: 20px;
				@include f(null, 26, 27); } } }
	&__structure {
		top: 350px;
		right: -94px;
		@include s(structure); }
	&__people {
		bottom: 0;
		left: 250px;
		@include s(people); }
	&__structure,
	&__people {
		position: absolute;
		@include lg {
			display: none; } } }


.logo {
	position: absolute;
	top: 0;
	right: 13px;
	width: 126px;
	font-size: 0;
	@include lg {
		width: 160px; }
	@include sm {
		right: 20px;
		width: 100px; }
	a {
		display: block; }
	img {
		width: 100%; } }
